import './styles.css';
import gsap from 'gsap';

const burgerRef = document.querySelector('.burger');
const sideBarRef = document.querySelector('.side-bar');
const closeButtonRef = sideBarRef.querySelector('.x-mark');
const navBarLinksRef = sideBarRef.querySelectorAll('.side-bar__link');

const openSideBar = () => {
  //sideBarRef.classList.add('active');
  gsap.set(navBarLinksRef, { x: -50, opacity: 0 });
  const timeline = gsap.timeline();
  timeline.to(sideBarRef, { x: '0%', duration: 1 }).to(navBarLinksRef, {
    x: 0,
    opacity: 1,
    duration: 0.7,
    stagger: 0.2,
    ease: 'elastic',
  });
};

const closeSideBar = () => {
  //sideBarRef.classList.remove('active');
  const timeline = gsap.timeline();
  timeline
    .to(navBarLinksRef, {
      y: 25,
      opacity: 0,
      duration: 0.5,
      stagger: -0.2,
    })
    .to(sideBarRef, { x: '-100%', duration: 0.5 });
};

burgerRef.addEventListener('click', openSideBar);
closeButtonRef.addEventListener('click', closeSideBar);
